﻿/*
    Used to manage styles related to Rock's store
    /* NOTE: This less file is not included in the core since it is only used
             internally. It must be included in the theme.
    
    1. Promo Rotator
    2. Package Summary
    3. Package Detail
    4. Components
    5. Purchased Package
    6. 
    7. 
*/

// 1. Promo Rotator
// -------------------------
.promotionrotator {
  margin-bottom: 48px;

  .carousel-inner {
    img {
      width: 100%;
    }
  }

  .carousel-indicators {
    bottom: -35px;

    li {
      border-color: #6a6a6a;
    }

    .active {
      background-color: #6a6a6a;
    }
  }

  .carousel-control {
    .fa-chevron-right {
      right: 20px;
    }

    .fa-chevron-left {
      left: 20px;
    }
  }
}

// 2. Package Summary
// -------------------------
.packagesummary {
  height: 250px;
  margin-bottom: 24px;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #ccc;

  .packagesummary-image-hover {
    width: 100%;
    height: 140px;
    content: '';
    background-color: #000;
    opacity: 0;
    -webkit-transition: opacity .25s ease-in;
       -moz-transition: opacity .25s ease-in;
        -ms-transition: opacity .25s ease-in;
         -o-transition: opacity .25s ease-in;
            transition: opacity .25s ease-in;
  }

  &:hover .packagesummary-image-hover {
    opacity: .5;
  }

  .packagesummary-body {
    padding: 8px;

    h2 {
      margin: 15px 0 0;
      overflow: hidden;
      font-size: 18px;
      font-weight: @font-weight-semibold;
      line-height: 1.3em;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    .vendor {
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}


// 3. 
// -------------------------

.packagedetail-image {
  width: 100%;
}

.packagedetail-additionalversions {
  display: none;
}

.btn-install {
  width: 100%;
}



// 4.
// -------------------------

// @fa-var-star: "\f005";
// @fa-var-star-half: "\f089";
// @fa-var-star-o: "\f006";

.rating {
  font-family: @fa-font-face;
  font-weight: @fa-theme-weight;
}

.pricelabel {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  text-align: center;
  border-radius: @border-radius-base;

  &.cost {
    color: @alert-success-text;
    background-color: @alert-success-bg;
    border-color: @alert-success-border;
  }

  &.free {
    color: @alert-info-text;
    background-color: @alert-info-bg;
    border-color: @alert-info-border;
  }

  h4 {
    font-size: 24px;
    font-weight: @font-weight-bold;
    color: inherit;
  }
}

// 5. Purchased Package
// -------------------------
.purchasedpackage {
  margin-bottom: 24px;

  h1 {
    margin-top: 0;
    overflow: hidden;
    font-size: 18px;
    font-weight: @font-weight-semibold;
    line-height: 1.3em;
    text-overflow: ellipsis;
    white-space: nowrap;
  }

  .purchasedpackage-install {
    a {
      width: 100%;
    }
  }
}


